            <img class="camera" src="../assets/cameralens.png" alt="camera lens" />
        </mq-layout>
        <mq-layout mq="md+" class="desk-camera">
            <div class="desk-slideshow camera" :style="{ backgroundImage: 'url(' + images[currentNumber % images.length] + ')' }" />

            <img class="camera" style="width: 25vw" src="../assets/cameralens.png" alt="camera lens" />
        </mq-layout>
