<script>
    export default {
        name: 'Camera',
        data() {
            return {
                images: [
                    'https://i.imgur.com/91Wg3c8.jpg', 
                    'https://i.imgur.com/bySV4jz.jpg', 
                    'https://i.imgur.com/HQV5V2Y.jpg', 
                    'https://i.imgur.com/q9sMQIe.jpg'
                ],
                currentNumber: 0,
                timer: null,
            }
        },
        mounted() {
            this.startRotation()
        },
        methods: {
            startRotation: function() {
                this.timer = setInterval(this.next, 2000)
            },
            next: function() {
                this.currentNumber += 1
            }
        }
    }
</script>

